.register{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    form{
        width: 400px;
        padding: 20px 15px;
        border-radius: 20px;
        display: flex;
        flex-direction: column;
        box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.75);
        align-items: center;
        background: #fda3c4;
    }
    input{
        width: 100%;
        height: 40px;
        margin-top: 20px;
        font-size: 20px;
        outline: none;
        border-radius: 10px;
        border: none;
        text-align: center;
    }
    button{
width: 50%;
height: 35px;
margin-top: 20px;
border-radius: 10px;
border: none;
text-align: center;
cursor: pointer;
    }
    button:hover{
        background:  #fda3c4;
        border: 1px solid black;
    }
}